<template>
  <el-container class="page-container">
    <!-- 网页头部 -->
    <el-header>
      <el-page-header class="page-header" @back="goBack" content="个人主页">
      </el-page-header>
    </el-header>
    <!-- 网页主体 -->
    <el-container>
      <!-- 侧边栏 -->
      <el-aside class="user-tabs-container">
        <!-- 用户头像 -->
        <div class="user-avatar-container">
          <el-avatar :size="200" src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"></el-avatar>
        </div>
        <!-- 用户信息 -->
        <div class="userItem">
          <h2>个人信息</h2>
          <span>账号: {{ accoutnum }}</span><br />
          <span>用户名: {{ username }}</span>
        </div>
      </el-aside>
      <!-- 用户行为主体 -->
      <el-main class="page-main-contain">
        <el-tabs tab-position="top">
          <!-- 用户收藏 -->
          <el-tab-pane label="我的收藏">
            <el-table class="tableClass" :data="tableData">
              <el-table-column prop="type" align="center" label="类型" width="130px">
              </el-table-column>
              <el-table-column prop="name" align="center" label="名称" width="200px">
              </el-table-column>
              <el-table-column prop="download" align="center" label="下载量" width="130px">
              </el-table-column>
              <el-table-column prop="description" align="center" label="描述性息" width="230px" show-overflow-tooltip>
              </el-table-column>
              <el-table-column align="center" label="操作">
                <template slot-scope="scope">
                  <el-button @click="deleteBookItem(scope.$index, scope.row)" icon="el-icon-star-on" circle></el-button>
                </template>
              </el-table-column>
            </el-table>
            <!-- 用户上传书籍 -->
          </el-tab-pane>
          <!-- 用户上传 -->
          <el-tab-pane label="我的上传">
            <el-button icon="el-icon-upload2" @click="uploadDialogVisiable = true">
            </el-button>
            <!-- 上传弹窗 -->
            <el-dialog title="上传书籍" :visible.sync="uploadDialogVisiable" width="60%">
              <el-upload class="upload-demo" drag action="https://jsonplaceholder.typicode.com/posts/" multiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">
                  将文件拖到此处，或<em>点击上传</em>
                </div>
                <div class="el-upload__tip" slot="tip">
                  只能上传jpg/png文件，且不超过500kb
                </div>
              </el-upload>
            </el-dialog>
          </el-tab-pane>
        </el-tabs>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      // 客户端存储用户名和账号名
      username: "",
      accoutnum: 0,
      // 收藏表格数据
      tableData: [],
      // 上传窗口
      uploadDialogVisiable: false,
    };
  },
  created() {
    // 从客户端的sessionStorage中获取用户名和账号
    this.username = window.sessionStorage.getItem("name");
    this.accoutnum = window.sessionStorage.getItem("id");
    // 未登录弹窗
    if (!this.username || !this.accoutnum) {
      this.$alert("您还未登录，请前往登录！", "提示", {
        confirmButtonText: "确定",
        type: "warning",
        center: true,
      })
        .then(() => {
          this.$router.push("/login");
        })
        .catch(() => { });
    }
    // 从后端api初始化tableData
    let init = async () => {
      const { data: res } = await this.$http.get(
        "/books/favorites/" + this.accoutnum
      );
      this.tableData = res.data;
    };
    // init();
    // 自定义数据
    for (var i = 0; i < 5; i++) {
      this.tableData.push({
        type: "CS",
        name: "CSAPP",
        download: i,
        description: "xxx",
      });
    }
  },
  methods: {
    // 返回主页
    goBack() {
      this.$router.push("/home");
    },
    // 删除收藏书籍
    deleteBookItem(index, row) {
      // 弹窗询问
      this.$confirm("是否取消收藏该书籍", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        // 确定按钮逻辑
        // 删除页面上表单
        this.tableData.splice(index, 1);
        // 向后台发送删除请求
        let dbi = async () => {
          const { data: res } = await this.$http({
            method: "get",
            url: "/books/favorites/cancel/" + this.accoutnum + "/" + row.id,
          });
          if (res.flag) {
            // 删除成功提示
            this.$message({
              type: "success",
              message: "已不再收藏该书籍!",
            });
          }
        };
        dbi();
      });
    },
  },
};
</script>

<style lang="less" scoped>
//主体容器
.page-container {
  line-height: 30px;
}

//页头容器
// .el-page-header {
// }

// 侧边标签栏
.user-tabs-container {
  width: 300px;
  text-align: center;

  // 头像容器
  .user-avatar-container {
    margin: 10px auto;
  }

  // 个人信息
  .userItem {
    font-size: 20px;
  }

  // 收藏部分容器
  .page-main-contain {
    width: 800px;
  }
}
</style>